.button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 56px;
  width: 100%;
  border: none;
    
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  text-align: center;
  color: #fff;
  background-color: $colorRed;
  text-decoration: none;
  cursor: pointer;

  transition: all 0.3s;

  &:hover {
    background-color: #ae1c23;
  }

  &:active {
    background-color: #eb343d;
  }

  &-view {
    @include onTablet {
      padding-left: 100px;
      padding-right: 100px;
    }

    @include onDesktop {
      width: 324px;
      padding-left: 348px;
      padding-right: 348px;
    }
  }
}
