.header {
  background-image: url(../images/header.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
    
  &__content {
    display: flex;
    flex-direction: column;
        
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;

    @include onTablet {
      padding: 50px 0;
    }
  }
  
  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
   
    &-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 66px;
  
      @include onTablet {
        width: 110px;
      }
    }
  }
  
  &__menu-opener {
    background-image: url(../images/menu.svg);
      
    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }
      
  &__title {
    margin: 125px 0 0;
    font-weight: 700;
    font-size: 40px;
    line-height: 100%;
    text-align: center;
    letter-spacing: -1.5px;
    color: #fff;

    @include onTablet {
      margin: 150px 0 0;
      font-size: 64px;
      line-height: 64px;
    }
  
    @include onDesktop() {
      margin: 80px 0 0;
      font-size: 80px;
      line-height: 80px;
    }
  }
}
