.icon {
  display: block;
  width: 18px;
  height: 18px;
  background-image: url(../images/phone.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;
  
  @include onTablet {
    width: 24px;
    height: 24px;
  }
  
  &__cross {
    background-image: url(../images/cross.svg);
  }

  &__tooltip {
    display: block;
    position: relative;
    top: 50%;
    transform: translate(-100%, -50%);
        
    width: max-content;
    padding-right: 20px;
      
    color: #fff;
    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    text-align: right;
    text-transform: uppercase;
    text-shadow: 18px 4px 26px rgba(0, 0, 0, 0.44);
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s;

    &-first {
      margin-bottom: 8px;
    }
  }

  @include onDesktop {
    &:hover &__tooltip {
      opacity: 1;
      pointer-events: all;
    }
  }
}

.icon__menu {
  display: block;
  width: 18px;
  height: 18px;
  background-image: url(../images/phone.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;
  
  @include onDesktop {
    width: 24px;
    height: 24px;
  }

  &__cross {
    background-image: url(../images/cross.svg);
  }
}
