.explore {
  &__section {
    img {
      transition-duration: 1s;

      &:hover {
        transform: scale(1.1);
        transition-duration: 1s;
      }
    }

    @include onTablet {
      padding-right: 100px;
      padding-left: 100px;
    }
       
    @include onDesktop {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      column-gap: 24px;
      padding-right: 0;
      padding-left: 0;
    }

    &-block-1 {
      @include onDesktop {
        grid-column: 1 / 5;
      }
    }
      
    &-block-2 {
      @include onDesktop {
        grid-column: 5 / 9;
      }
    }
    
    &-block-3 {
      @include onDesktop {
        grid-column: 9/ 13;
      }
    }
  }

  &__place {
    margin-bottom: 48px;
    margin-top: 40px;
 
    &-photo-container {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      column-gap: 24px;
      margin-bottom: 32px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    text-align: center;
    color: $colorRed;
  }

  &__about {
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    color: $colorBlack;
    margin: 0;
  }

  &__photo {
    width: 100%;
    grid-column: 1 / 5;

  }
}
