.contact-us {
  &__place {

    a {
      text-decoration: none;
    }

    @include onTablet {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      column-gap: 24px;
    }
       
        
    &-container-3 {
      grid-column: 7 / 13;
      margin-top: 48px;

      @include onDesktop {
        grid-column: 8 / 13;
      }
    }

    &-containers {
      grid-column: 1 / 7;
          
      @include onDesktop {
        display: flex;
        justify-content: space-between;
      }
    }
  }

  &__info {
    margin-bottom: 48px;
    margin-top: 48px;
    
    &-title {
      font-weight: 700;
      font-size: 16px;
      line-height: 27px;
      color: $colorBlack;
      margin-bottom: 16px;
    }

    &__phone {
      font-weight: 700;
      font-size: 16px;
      line-height: 27px;
      color: #3e3e3e;
      
      box-shadow: inset 0 0 0 0 #3e3e3e;
      margin: 0 0 8px -0.25rem;
      padding: 0 0.25rem;
      transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;

      &:hover {
        box-shadow: inset 200px 0 0 0 #3e3e3e;
        color: white;
      }
    }

    &__phone-title {
      font-weight: 400;
      font-size: 14px;
      line-height: 27px;
      color: $colorBlack;
      margin-bottom: 4px;
    }

    &__address {
      font-weight: 700;
      font-size: 16px;
      line-height: 140%;
      color: #3e3e3e;

      box-shadow: inset 0 0 0 0 #3e3e3e;
      margin: 0 -0.25rem;
      padding: 0 0.25rem;
      transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;

      &:hover {
        box-shadow: inset 200px 100px 0 0 #3e3e3e;
        color: white;
      }
    }

    &__address-title {
      font-weight: 400;
      font-size: 14px;
      line-height: 27px;
      color: $colorBlack;
      margin-bottom: 4px;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}
