.form-field {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 48px;
  padding: 14px 16px;
  border: 1px solid transparent;
  background-color: #f8f8fa;
  color: $colorBlack;

  font-family: "Cinzel", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #7e7e83;
  }

  &:hover {
    border-color: #d5d7de;
    color: $colorBlack;
  }

  &:focus {
    border-color: $colorBlack;
    color: $colorBlack;
    outline: none;
  }

  &--textarea {
    height: 144px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: $colorBlack;
    resize: none;
  }
}
