.logo {

  &__image {
    height: 46px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    @include onTablet() {
      height: 65px;
    }
  }
}

.logo__menu {

  &__image {
    height: 46px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    @include onDesktop() {
      height: 65px;
    }
  }
}
