.menu {
  background-color: $colorRed;
  overflow: hidden;
  
  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;

    &__header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 40px;
    }
  }

  &__list {
    margin: 0 0 23px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 23px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
      
    color: #fff;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    font-style: normal;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-decoration: none;
    transition: border-color 0.3s;

    &:hover {
      border-color: #fff;
    }
  }

  &__text {
    color: #fff;
    font-weight: 700;
    font-size: 16px;
    font-style: normal;
    line-height: 27px;
    text-transform: uppercase;
    margin-bottom: 8px;

    &-last {
      margin-bottom: 16px;
    }

    &__phone {
      color: #fff;
      font-weight: 700;
      font-size: 16px;
      font-style: normal;
      line-height: 27px;
      text-transform: uppercase;
      text-decoration: none;

      box-shadow: inset 0 0 0 0 #3e3e3e;
      margin: 0 0 8px -0.25rem;
      padding: 0 0.25rem;
      transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;

      &:hover {
        box-shadow: inset 200px 0 0 0 #3e3e3e;
        color: white;
      }

      &-last {
        margin-bottom: 16px;
      }
    }
  }

  &__call {
    color: #fff;
    font-weight: 700;
    font-style: normal;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-decoration: none;
    padding-bottom: 8px;
    width: max-content;
    border-bottom: 1px solid transparent;
    margin-bottom: 30px;

    &:hover {
      border-color: #fff;
    }
  }
}
