.about {
  &__content {
    @include onTablet {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      column-gap: 24px;
    }
  }

  &__title {
    @include onTablet {
      grid-column: 1 / 6;
    }

    &-top {
      font-weight: 700;
      font-size: 12px;
      line-height: 140%;
      color: $colorRed;
    }

    &-under {
      margin: 0;
      font-weight: 700;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -1.5px;
      color: $colorBlack;
     
      @include onTablet {
        font-size: 48px;
        line-height: 48px;
      }
    }
  }

  &__description {
    @include onTablet {
      grid-column: 6 / 13;
    }

    &_title {
      margin: 24px 0 16px;
      font-weight: 700;
      font-size: 24px;
      line-height: 30px;
      color: $colorBlack;
          
      &-first {
        @include onTablet {
          margin: 0 0 16px;
        }
      }
    }
  }

  &__bold {
    font-weight: 700;
    margin-right: 25px;
  }

  &__container {
    display: flex;
    margin-bottom: 16px;
  }

  &__list {
    list-style: none;
    left: 0;
    top: 0;
    bottom: 0;
    padding: 0;
  }
}
