.place {
  &__photo-container {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    max-height: 320px;
    overflow: hidden;
        
    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onTablet {
      height: 186px;
      padding-bottom: 0;
    }

    @include onDesktop {
      height: 320px;
      padding-bottom: 0;
    }

    &-new {
      position: relative;
      box-sizing: border-box;
      width: 100%;
      max-height: 320px;
      overflow: hidden;
            
      &::after {
        content: "";
        display: block;
        padding-bottom: 100%;
      }
            
      @include onTablet {
        height: 284px;
        padding-bottom: 0;
      }
    }
  }

  &__photos {
    margin-bottom: 24px;

    img {
      transition-duration: 1s;
      
      &:hover {
        transform: scale(1.1);
        transition-duration: 1s;
      }
    }

    &_position {
      display: flex;
      gap: 24px;
        
      &-foto1 {
        flex-grow: 1;

        @include onTablet {
          flex-grow: 2;
        }
      }

      &-foto2 {
        flex-grow: 1;
      }

      &-foto3 {
        flex-grow: 1;

        @include onTablet {
          height: 284px;
          padding-bottom: 0;
        }
      }

      &-foto4 {
        flex-grow: 1;

        @include onTablet {
          height: 284px;
          padding-bottom: 0;
        }
      }

      &-foto5 {
        flex-grow: 1;

        @include onTablet {
          display: none;
        }

        @include onDesktop {
          display: flex;
        }
      }

      &-foto6 {
        flex-grow: 1;
      }

      &-foto7 {
        flex-grow: 1;

        @include onTablet {
          flex-grow: 2;
        }
      }
    }
  }
    
  &__photo {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__description {
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: $colorBlack;
        
    &-red {
      font-weight: 700;
      color: $colorRed;
    }

    &-title {
      text-align: center;
      margin: 8px 20px 48px;

      @include onTablet {
        margin: 8px 100px 48px;
      }

      @include onDesktop {
        margin: 8px 340px 48px;
      }
    }
  }
}
